import React, { useEffect, useState } from 'react';
import { getProjects } from '../../api'; // Импортируйте функцию для получения проектов
import { useAuth } from '../../AuthContext'; // Импортируйте useAuth
import { Link } from 'react-router-dom'; // Импортируйте Link

const TreeNodeHome = ({ node }) => {
  const hasChildren = node.children && node.children.length > 0; // Проверяем наличие вложенных детей
  const className = hasChildren ? "sidebar_menu-item-project group-list" : "sidebar_menu-item-project"; // Устанавливаем класс

  return (
    <li>
      <details>
        <summary>
          <div className={className}>
            <span className="styled-text">{node.shortname}</span>
            {hasChildren && <img src={require('../../assets/icons/chevron.png')} className='sidebar_menu-item-project-image' alt="" />}

            <Link to={`/all_documents/${encodeURIComponent(node.shortname)}`} className='sidebar_menu-item-project-link'>
              <img src={require('../../assets/icons/arrow2.png')} alt="Перейти" />
            </Link>
          </div>
        </summary>
        {hasChildren && (
          <ul style={{paddingLeft: '20px'}}>
            {node.children.map(child => (
              <TreeNodeHome key={child.id} node={child} />
            ))}
          </ul>
        )}
      </details>
    </li>
  );
};

const ProjectTreeHome = () => {
  const { token } = useAuth(); // Получаем токен из контекста
  const [treeData, setTreeData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchProjects = async () => {
      if (!token) {
        setError('Not authenticated. Please log in.');
        setLoading(false);
        return;
      }

      try {
        const data = await getProjects(token);
        if (data) {
          setTreeData(data);
        } else {
          setError('Failed to load data.');
        }
      } catch (err) {
        setError('Failed to load data.');
        console.error('Fetch error:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchProjects();
  }, [token]);

  if (loading) return <div>Loading...</div>;
  if (error) return <div style={{ color: 'red' }}>{error}</div>;

  return (
    <ul>
      {treeData.map(node => (
        <TreeNodeHome key={node.id} node={node} />
      ))}
    </ul>
  );
};

export default ProjectTreeHome;